<template>
	<div class="location"> 
    <div id="map">
      <img :src="pazzaImg" alt="">
    </div>
	</div>
</template>

<script>
export default {
  name: 'location',
  data () {
    return {
    	pazzaImg:require("../../../static/pazza1.jpg")
    }
  },
  components:{
  }, 
  
  methods:{
    
  },
  mounted(){
    let imgs=document.getElementsByTagName("img");
    Array.from(imgs).forEach(function(ele,index){
      ele.src=ele.src.split("static").join("pazzaSystem/static");
    })
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .location{
    margin-top: 8px;
    min-height: 500px;
  }
  #map{
    width:100%;
    /*height:400px;*/
    /*border:#ccc solid 1px;*/
    font-size:12px; 
    border:none; 
    float:left;
    border-radius: 5px;
    overflow: hidden;
  }
  #map img{
    width: 100%;
  }
</style>



