<template>
	<div class="menu">
    <div class="row">
      <div class="col-sm-6 col-md-3" v-for="foods in menuList">
        <img :src="foods.imgSrc" alt="">
        <div class="caption">
          <h3>{{foods.pName}}</h3>
          <p>{{foods.pazzaPrice}}</p>
          <p>
            <a href="" class="btn btn-primary">订餐</a>
            <a href="" class="btn btn-default">查看详情</a> 
          </p>
        </div>
      </div>
    </div>
    <div class="noMore" v-show="noMore"><p>没有数据了！！！</p></div>
	</div>
</template>

<script>
export default {
  name: 'menuList',
  data () {
    return {
      n:10,
      noMore:false,
      menuList:[
          {imgSrc:require("../../../static/pazza1.jpg"),pName:'Monkey',pazzaPrice:28,textTips:'至尊披萨是意大利比萨的一个品种,其品牌代言人为著名主持人李湘和李维嘉。'},
          {imgSrc:require("../../../static/pazza2.jpg"),pName:'Dog',pazzaPrice:28,textTips:'至尊比萨从本质上改变了人们头脑中饼状比萨的传统概念'},
          {imgSrc:require("../../../static/pazza3.jpg"),pName:'Snake',pazzaPrice:28,textTips:'该配方主要食材有酱牛肉、面粉、马苏里拉奶酪、黑胡椒、青椒、圣女果等。'},
          {imgSrc:require("../../../static/pazza4.jpg"),pName:'Bear',pazzaPrice:28,textTips:'以金黄香浓的车打芝士酱和蘑菇等烤制，美味口中尽享'},
          {imgSrc:require("../../../static/pazza5.jpg"),pName:'Tiger',pazzaPrice:28,textTips:'底层芝士马苏里拉和车打芝士底层芝士马苏里拉和车打芝士'},
          {imgSrc:require("../../../static/pazza1.jpg"),pName:'Horse',pazzaPrice:28,textTips:'蕃茄酱2汤匙(30ml) ，奶酪100G ，火腿50G ，口蘑50G ，香菇20G ，洋葱20G '},
          {imgSrc:require("../../../static/pazza2.jpg"),pName:'Cat',pazzaPrice:28,textTips:'蕃茄酱2汤匙(30ml) ，奶酪100G ，火腿50G ，口蘑50G ，香菇20G ，洋葱20G '},
          {imgSrc:require("../../../static/pazza3.jpg"),pName:'Zebra',pazzaPrice:28,textTips:'底层芝士马苏里拉和车打芝士底层芝士马苏里拉和车打芝士'},
          {imgSrc:require("../../../static/pazza4.jpg"),pName:'Lion',pazzaPrice:28,textTips:'底层芝士马苏里拉和车打芝士底层芝士马苏里拉和车打芝士'},
          {imgSrc:require("../../../static/pazza5.jpg"),pName:'Deer',pazzaPrice:28,textTips:'底层芝士马苏里拉和车打芝士底层芝士马苏里拉和车打芝士'},
          {imgSrc:require("../../../static/pazza4.jpg"),pName:'Kangaroo',pazzaPrice:28,textTips:'底层芝士马苏里拉和车打芝士底层芝士马苏里拉和车打芝士'},
          {imgSrc:require("../../../static/pazza1.jpg"),pName:'Monkey',pazzaPrice:288,textTips:'底层芝士马苏里拉和车打芝士底层芝士马苏里拉和车打芝士'}
      ]
    	
    }
