<template>
	<div class="slide">
		<swiper v-if="swiperSlides.length>1" :options="swiperOption">
		    <swiper-slide  v-for="slide in swiperSlides"><img v-bind:src="slide.imgSrc" alt=""></swiper-slide>
		    <div class="swiper-pagination" slot="pagination"></div>
		    <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
		    <div class="swiper-button-next swiper-button-white" slot="button-next"></div>	
		</swiper>
		<w-recommend/>
	</div>
	
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import wRecommend from '../recommendToday/Recommend'

export default {
  name: 'wslide',
  data () {
    return {
    	title:'今日推荐的pazza!',
    	swiperOption:{
    		autoplay:true,
    		observer: true,//修改swiper自己或子元素时，自动初始化swiper
          	observeParents: true,//修改swiper的父元素时，自动初始化swiper
    		loop:true,
    		speed:1000,
    		prevButton:'.swiper-button-prev',
    		nextButton:'.swiper-button-next',
    		//autoplayDisableOnInteraction:false,
    		pagination:{
	    		 el:'.swiper-pagination',
	    		 clickable:true
	    	},
	    	navigation:{
	    		prevEl:'.swiper-button-prev',
	    		nextEl:'.swiper-button-next',
	    	}		
    	},
    	swiperSlides:[
    		{imgSrc:require("../../../static/pazza1.jpg"),active:true},
    		{imgSrc:require("../../../static/pazza2.jpg"),active:false},
    		{imgSrc:require("../../../static/pazza3.jpg"),active:false},
    		{imgSrc:require("../../../static/pazza4.jpg"),active:false},
    		{imgSrc:require("../../../static/pazza5.jpg"),active:false}
    	]
    }
  },
  components:{
  	swiper,
  	swiperSlide,
  	wRecommend
  },
  methods:{
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.swiper-container{
		height: 400px;
	}
	.swiper-container img{
		width: 100%;
		height: 100%;
	}
</style>



